</div>
</template>

<script>
export default {
  data() {
    return {

    }
  },
  methods:{

  },
  created() {
    //在这里接收code
    //localhost:8080/callback?code=RWAk6H&state=normal
    var url = window.document.location.href.toString();//http://127.0.0.1:8080/callback?code=c31e6068b82951529c8b4265961405b74a9832abc3992bd2d46fcb626652a668

    //接收到code之后,发送请求到后台的callback接口
    // url=url.replace("8080","9001")
    var strings = url.split("?");


    this.axios.get("http://localhost:8083/goods/getAccessToken?"+strings[1]).then(resp=>{
      // alert(resp.data.access_token)
      console.log(resp.data)
      //获取到用户名了,将用户名存入localStorage中
      localStorage.setItem("username",resp.data.user_name)
      //跳转到首页
      this.$router.push('/home')
    })
  }
}
</script>

