<template>
  <div class="aa">

    <el-row>
      <el-button @click="btn">测试axios</el-button>
      <el-button type="primary" @click="mayunLogin">码云登录</el-button>
      <el-button type="success" @click="myLogin">my登录</el-button>
      <el-button type="info" @click="myLogin">信息按钮</el-button>
      <el-button type="warning">警告按钮</el-button>
      <el-button type="danger">危险按钮</el-button>
    </el-row>
  </div>
</template>
<script>

export default {
  data() {
    return {
      oahth2url:''
    }
  },
  methods:{
    btn(){
      this.axios.get("http://localhost:8888/api/user/configinfo").then(resp=>{
        alert(resp.data)
      })
    },
    mayunLogin(){
      //将用户引导至第三方登录页面
      window.open("https://gitee.com/oauth/authorize?client_id=7fb8c7c5f6061a82c2c43f300657185a20a3b49faad7e39cfa1403505628e3cb&redirect_uri=http://127.0.0.1:8080/callback&response_type=code")
    },
    myLogin(){
      //将用户引导至第三方登录页面
      window.open(this.oahth2url)
    }
  },
  mounted() {
    this.axios.get("http://localhost:8083/goods/getOauth2CodeUrl").then(resp=>{
      this.oahth2url=resp.data
    })
  }
}
</script>