<template>
  <div >
    <el-row :gutter="20">
      <el-col :span="12" :offset="6">
        <div class="grid-content">
            <el-form  :model="ruleForm" status-icon  ref="ruleForm" label-width="100px" class="demo-ruleForm">

              <el-form-item  label="" prop="questions">
                <el-input label-width:50px v-model.number="ruleForm.questions"></el-input>
              </el-form-item>
              <!--        <el-form-item label="" prop="answer">
                        <el-input v-model.number="ruleForm.answer"></el-input>
                      </el-form-item>-->
              <el-form-item>
                <el-button type="primary" @click="ask">提问</el-button>
              </el-form-item>
            </el-form>
            <h5 v-html="ruleForm.answer">

            </h5>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>

export default {
  data() {
    return {
      ruleForm:{
        questions:'',
        answer:''
      }

    }
  },
  methods:{
    async  init(){
      const { Configuration, OpenAIApi } = require("openai");
      const configuration = new Configuration({
        apiKey: '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',
      });
      const openai = new OpenAIApi(configuration);

      const response = await openai.createCompletion({
        model: "text-davinci-003",
        prompt: this.ruleForm.questions,
        temperature: 0.5,
        max_tokens: 4000,
        top_p: 1,
        frequency_penalty: 0.5,
        presence_penalty: 0.5,
        stop: ["HUMAN","AI"],
      });

      this.ruleForm.answer=response.data.choices[0].text
    },
    ask() {
      this.init()
    }
  }

}
</script>
